<template>
  <!-- 点击汇款记录新建进入回款信息界面 -->
  <div class="con_box">
    <el-tabs class="eltabs" v-model="activeName" @tab-click="handleClick">
      <el-tab-pane
        class="eltabpane"
        label="回款信息"
        name="collectioninfomation"
      >
        <collectioninfomation ref="collectioninfomation"></collectioninfomation>
      </el-tab-pane>
    </el-tabs>
    
    <div class="return_btn">
      <el-button
        v-if="activeName == 'collectioninfomation'"
        size="mini"
        type="primary"
        @click="saveBtn()"
        >保存</el-button
      >
      <el-button v-if="isouter==1" size="mini" @click="returnBtn()">返回</el-button>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, getCurrentInstance, onMounted } from "vue";
import collectioninfomation from "./collectioninfomation.vue";
export default {
  components: { collectioninfomation },
  setup() {
    const { proxy } = getCurrentInstance();
    console.log("11111111111");
    console.log(proxy.$route);
    const state = reactive({
      activeName: "collectioninfomation",
      isedit: null,
      isouter:null
    });
    const handleClick = (tab, event) => {
      console.log(tab);
      console.log(event);
    };
    const saveBtn = () => {
      proxy.$router.push("/contract");
    };
    const returnBtn = () => {
      proxy.$store.state.isedit = 1
        proxy.$router.go(-1)
    };
    onMounted(() => {
      proxy.isedit = proxy.$route.query.isedit;
      proxy.isouter = proxy.$route.query.isouter
    });

    return {
      handleClick,
      saveBtn,
      returnBtn,
      ...toRefs(state),
    };
  },
};
</script>

<style scoped>
.con_box {
  background: #fff;
  height: 100%;
  position: relative;
}
.return_btn {
  position: absolute;
  right: 10px;
  top: 5px;
}
</style>
<style>
.eltabs.el-tabs .el-tabs__nav-scroll {
  padding: 0 10px;
  box-sizing: border-box;
}
.eltabs.el-tabs .el-tabs__content {
  padding: 0 10px;
  padding-top: 10px;
  box-sizing: border-box;
  background: #fff;
}
</style>